import * as React from 'react';
import * as ReactDOM from 'react-dom/client';

import { createPortal } from 'react-dom';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';

import App from './App';

const container = document.getElementById('wentilabs-chat-root-element');

if (!container) {
  throw new Error('Failed to find the root element');
}

const root = ReactDOM.createRoot(container);
const queryClient = new QueryClient();

root.render(
  <QueryClientProvider client={queryClient}>
    <React.StrictMode>
      {createPortal(
        <App
          useCaseId={(container as HTMLElement).dataset.usecaseid || '1'}
          welcomeMessage={(container as HTMLElement).dataset.welcomemessage || 'Hi, how can i help you?'}
          botName={(container as HTMLElement).dataset.botname || 'WentiLabs Assistant'}
        />,
        document.body,
        'wentilabs-chat-root-element-key',
      )}
    </React.StrictMode>
  </QueryClientProvider>,
);
